
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import * as util from '@/assets/js/util.js'
import { axiosPost,axiosGet } from '@/axios'
import { mapState } from 'vuex'

export default {
name: 'Login',
components:{
Group,
XInput,
XButton,
},
computed: {
...mapState([
'SIMID',
'Access_token'
])
},
data(){
return {
userName: 'admin',
password: 'A1s2d3',
rememberTag:false
}
},
mounted(){
let user = util.getCookie("userName")
let pwd = util.getCookie("password")
if(user != '' && pwd != ''){
this.userName = user
this.password = pwd
let params = {
url: '/MAccount/login2',
data:{
Account: this.userName,
Password: this.password,
SIMID: this.$store.state.SIMID
}
}
axiosPost(params.url,params.data).then(res => {
this.$vux.toast.text('登录成功')
this.$router.replace({path: '/home'})
},err => {
this.$vux.toast.text(err.Message)
})
}
},
methods:{
rememberUser(ev){
if(!this.userName.trim() || !this.password.trim()){
this.$vux.toast.text('请先输入用户名和密码')
ev.preventDefault()
return
}
this.rememberTag = !this.rememberTag
if(!this.rememberTag){
util.delCookie("userName");
util.delCookie("password");
}else{
util.setCookie("userName",this.userName,30);
util.setCookie("password",this.password,30);
}
},
handleSubmit(){
if(!this.userName.trim()){
this.$vux.toast.text('请输入用户名')
return
}
if(!this.password.trim()){
this.$vux.toast.text('请输入密码')
return
}
let params = {
url: '/MAccount/login2',
data:{
Account: this.userName,
Password: this.password,
SIMID: this.$store.state.SIMID
}
}
axiosPost(params.url,params.data).then(res => {
this.$vux.toast.text('登录成功')
this.$router.replace({path: '/home'})
this.$store.dispatch('addAccessToken',res.Token_type+' '+res.Access_token)
},err => {
this.$vux.toast.text(err.Message)
})
},
getFingerprint(){
let fingerprint = new Fingerprint().get()
console.log(fingerprint)
}
}
}



