/* 
    更改 Vuex 的 store 中的状态的唯一方法是提交 mutation

    每个 mutation 都有一个字符串的 事件类型 (type) 和 一个 回调函数 (handler)。
    这个回调函数就是我们实际进行状态更改的地方，并且它会接受 state 作为第一个参数

    Mutation 必须是同步函数!

*/

import * as types from './mutation-types'

const mutations = {
    // 存储access_token
    addAccessToken(state,payload){
        state.Access_token = payload
    },
    // payload 载荷-> n   在大多数情况下，载荷应该是一个对象，这样可以包含多个字段并且记录的 mutation 会更易读
    addCount(state,n){
        state.count += n
    },
    // 以对象的方式提交，整个对象都作为载荷传给 mutation 函数，因此 handler 保持不变。 见actions.js
    minuxCount(state,payload){
        state.count -= payload.amount
    },
    // 我们可以使用 ES2015(ES6) 风格的计算属性命名功能来使用一个常量作为函数名
    [types.CHANGE_LOGIN_STATE](state){
        state.login = !state.login
    },
    [types.SOME_MUTATION] (state,payload) {
        state.userInfo.username = payload
    }
}

export default mutations


