// sessionStorage
import instance from 'axios'

export const session = function(key, value) {
  if (value === void 0) {
    var lsVal = sessionStorage.getItem(key)
    if (lsVal && lsVal.indexOf('autostringify-') === 0) {
      return JSON.parse(lsVal.split('autostringify-')[1])
    } else {
      return lsVal
    }
  } else {
    if (typeof value === 'object' || Array.isArray(value)) {
      value = 'autostringify-' + JSON.stringify(value)
    }
    return sessionStorage.setItem(key, value)
  }
}

export const phoneReg = function(num) {
  let reg = /^1[3456789]\d{9}$/gi
  if (reg.test(num)) {
    return true
  } else {
    return false
  }
}
export const numberReg = function(num) {
  let reg = /\D+/gi
  if (reg.test(num)) {
    return false
  } else {
    return true
  }
}

// ajax错误处理
export const catchError = function(error) {
  if (error.response) {
    // 401 session为空  402token时效 403token为空
    let arr = [401, 402, 403, 404]
    let msg = {
      '401': 'session为空',
      '402': 'token失效',
      '403': 'token为空'
    }
    if (arr.indexOf(parseInt(error.response.status)) > -1) {
      if (process.env.NODE_ENV !== 'production') {
        console.log(parseInt(error.response.status))
      }
      if (
        process.env.NODE_ENV !== 'production' ||
        process.env.ERROR_FLAG === 'test'
      ) {
        console.log(
          msg[parseInt(error.response.status)] + '本地session已经清除'
        )
      } else {
        sessionStorage.removeItem('token')
        sessionStorage.removeItem('sessionid')
        location.reload()
      }
    } else {
      console.warn(error)
    }
  }
  return Promise.reject(error)
}

// 统一格式化数据接口
export const dataFormate = function ConvertToTreeGridJson(
  rows,
  idFieldName,
  pidFieldName,
  fileds,
  isCallBack
) {
  function exists(rows, ParentId) {
    for (var i = 0; i < rows.length; i++) {
      if (rows[i][idFieldName] === ParentId) {
        return true
      }
    }
    return false
  }

  var nodes = []
  // get the top level nodes
  for (var i = 0; i < rows.length; i++) {
    var row = rows[i]
    if (!exists(rows, row[pidFieldName])) {
      var data = {
        id: row[idFieldName]
      }
      var arrFiled = fileds.split(',')
      for (var j = 0; j < arrFiled.length; j++) {
        if (arrFiled[j] !== idFieldName) {
          data[arrFiled[j]] = row[arrFiled[j]]
        }
      }
      nodes.push(data)
    }
  }
  // console.info('根目录nodes：' + JSON.stringify(nodes))
  var toDo = []
  for (let i = 0; i < nodes.length; i++) {
    toDo.push(nodes[i])
  }
  let count = 1
  toDo.push(count)
  while (toDo.length) {
    var node = toDo.shift() // the parent node
    if (isCallBack) {
      if (node === 1) {
        toDo.push(++count)
      }
      if (node === 2) {
        toDo.map(item => {
          item.isEnd = true
        })
        toDo.push(++count)
      }
      if (node === 3) {
        toDo.push(++count)
      }
    }
    // get the children nodes
    for (let i = 0; i < rows.length; i++) {
      let row = rows[i]
      if (row[pidFieldName] === node.id) {
        var child = {
          id: row[idFieldName]
        }
        let arrFiled = fileds.split(',')
        for (let j = 0; j < arrFiled.length; j++) {
          if (arrFiled[j] !== idFieldName) {
            child[arrFiled[j]] = row[arrFiled[j]]
          }
        }
        if (node.children) {
          node.children.push(child)
        } else {
          node.children = [child]
        }
        // 循环寻找children
        toDo.push(child)
      }
    }
  }
  return nodes
}

// 课程数据格式化
export const classFormate = function(data) {
  let obj = {}
  data.map(item => {
    if (obj[`${item.gradeName}-${item.gradeId}`]) {
      obj[`${item.gradeName}-${item.gradeId}`].push(item)
    } else {
      obj[`${item.gradeName}-${item.gradeId}`] = []
      obj[`${item.gradeName}-${item.gradeId}`].push(item)
    }
  })
  return obj
}

// 权限过滤
export const filterPropety = function(obj, flag) {
  return obj.some(item => {
    return item.permissionName === flag
  })
}

// 格式化resPeople type 是label attr shi  value
export const formateLists = function(arr, type, attr, callBack) {
  let key2val = []
  if (arr.length) {
    arr.map(item => {
      let obj = {
        value: attr ? item[attr] : item.id,
        label: type ? item[type] : item.realName
      }
      // 可插入其他属性
      callBack && callBack(obj)
      key2val.push(obj)
    })
  }
  return key2val
}
export const formateException = function(num) {
  if (!num) {
    return 0
  } else {
    return num
  }
}
// formate props
export const formateProperty = function(lists, ahead) {
  let obj = []
  if (lists.length) {
    lists.map(item => {
      obj.push(item.permissionName.split(ahead)[1])
    })
  }
  return obj
}

// 搜索字典方法
export const searchDict = function(dicType) {
  const URL = 'dict/queryDictList'
  return instance.post(URL, { type: dicType })
}

// assertArrayItem
export const assertArrayItem = function(arr, item) {
  if (arr.indexOf(item) > -1) {
    return true
  } else {
    return false
  }
}

// setcookie
export const setCookie = function(cname,cvalue,exdays){
  var d = new Date();
  d.setTime(d.getTime()+(exdays*24*60*60*1000));
  var expires = "expires="+d.toGMTString();
  document.cookie = cname+"="+cvalue+"; "+expires;
}

// getCookie
export const getCookie = function(cname){
  var name = cname + "=";
  var ca = document.cookie.split(';');
  for(var i=0; i<ca.length; i++) {
      var c = ca[i].trim();
      if (c.indexOf(name)==0) { return c.substring(name.length,c.length); }
  }
  return "";
}

// delCookie
export const delCookie = function(name){
  var exp = new Date();
  exp.setTime(exp.getTime() - 1);
  var cval=getCookie(name);
  if(cval!=null)
  document.cookie= name + "="+cval+";expires="+exp.toGMTString();
}


