
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'Regist',
data(){
return {
userName: '',
password: ''
}
},
methods:{
handleSubmit(){
if(!this.userName.trim()){
this.$vux.toast.text('请输入系统用户名')
return
}
if(!this.password.trim()){
this.$vux.toast.text('请输入系统密码')
return
}
let params = {
url: '/movie/top250',
data:{
userName: this.userName,
password: this.password
}
}
this.$axios.post(params.url).then(res => {
console.log(res)
})
.catch(err => {
console.log(err)
})
}
},
components:{
Group,
XInput,
XButton
}
}



