/*
    getter 相当于store的计算属性。就像计算属性一样，getter 的返回值会根据它的依赖被缓存起来，且只有当它的依赖值发生了改变才会被重新计算

    getter 接受 state 作为其第一个参数 ，也可以接受其他 getter 作为第二个参数

*/ 

const getters = {
    doneTodos: state => {
        return state.todos.filter(todo => todo.done)
    },
    doneTodosCount: (state, getters) => {
        return getters.doneTodos.length
    },
    // 也可以通过让 getter 返回一个函数，来实现给 getter 传参。在你对 store 里的数组进行查询时非常有用
    getTodoById: (state) => (id) => {
        return state.todos.find(todo => todo.id === id)
    },
    anotherTodo: () =>{
        return "我是getters中的其他方法";
    },
    // todoConsole: () =>{
    //     console.log("我是getters中的打印，不需要调用即可立即执行");
    // }
}

export default getters


