import HelloWorld from '@/components/HelloWorld'
import HelloFromVux from '@/components/HelloFromVux'
import VuexDemo from '@/components/VuexDemo'
import Upload from '@/components/Upload'
import Upload2 from '@/components/Upload2'
import GetLocation from '@/components/GetLocation'
import Login from '@/pages/Login'
import Regist from '@/pages/Regist'
import RegistAgain from '@/pages/RegistAgain'
import Home from '@/pages/Home'

const routes = [
    // 默认跳转
    {
        path: '/',
        redirect: {
            name: 'Login'
        }
    },
    {
        path: '/login',
        name: 'Login',
        component: Login
    },
    {
        path: '/regist',
        name: 'Regist',
        component: Regist
    },
    {
        path: '/home',
        name: 'Home',
        component: Home
    },
    {
        path: '/registAgain',
        name: 'RegistAgain',
        component: RegistAgain
    },
    {
        path: '/hello',
        name: 'HelloWorld',
        component: HelloWorld
    },
    {
        path: '/vux',
        name: 'HelloFromVux',
        component: HelloFromVux
    },
    {
        path: '/vuex',
        name: 'VuexDemo',
        component: VuexDemo
    },
    {
        path: '/upload',
        name: 'Upload',
        component: Upload
    },
    {
        path: '/upload2',
        name: 'Upload2',
        component: Upload2
    },
    {
        path: '/location',
        name: 'GetLocation',
        component: GetLocation
    }
]

export default routes


