import { mapState,mapGetters,mapMutations } from 'vuex'

export default {
    name: 'VuexDemo',
    computed: {
        ...mapState([
            'orderList',
            'orderDetail',
            'login',
            'userInfo'
        ]),
        // ...mapGetters([
        //     'doneTodos',
        //     'doneTodosCount',
        //     'getTodoById',
        //     'anotherTodo'
        // ])
        
        // 如果想将一个 getter 属性另取一个名字，使用对象形式：
        ...mapGetters({
            doneTodos: 'doneTodos',
            doneTodosCount: 'doneTodosCount',
            getTodoById: 'getTodoById',
            anotherTodo: 'anotherTodo'
        })
    },   
    mounted(){
        /* 
            mapState通过扩展运算符将store.state.orderList 映射this.orderList  这个this 很重要，这个映射直接映射到当前Vue的this对象上。
            所以通过this都能将这些对象点出来，同理，mapActions, mapMutations都是一样的道理
        */ 
        console.log(this.$store)
        // console.log(orderList)      // undefind
        console.log(this.login)        // false
        console.log(this.orderList)    // object

        /*
            mapGetters 辅助函数仅仅是将 store 中的 getter 映射到局部计算属性：
        */ 
        console.log(this.doneTodos)
        console.log(this.doneTodosCount)
        console.log(this.getTodoById(2))
        console.log(this.anotherTodo)
    },
    methods: {
        /*
            可以在组件中使用 this.$store.commit('xxx') 提交 mutation
            或者使用 mapMutations 辅助函数将组件中的 methods 映射为 store.commit 调用（ 需要在根节点注入 store ）
        */
        add(){
            this.$store.dispatch('add')
        },
        minux(){
            this.$store.dispatch('jian')
        },
        changeLogin(){
            this.$store.dispatch('changeLoginState')
        },
        ...mapMutations({
            someMutation: 'SOME_MUTATION'
        })
    }
}



