import * as types from './mutation-types'

const actions = {
    addAccessToken({commit},a){
        commit('addAccessToken',a)
    },
    add({commit}){
        setTimeout(function(){
            commit('addCount',2)
        },2000)
    },
    jian({commit}){
        // commit('minuxCount',1)

        // 以对象的方式提交，整个对象都作为载荷传给 mutation 函数，因此 handler 保持不变
        commit({
            type: 'minuxCount',
            amount: 1
        })
    },
    changeLoginState({commit}){
        commit(types.CHANGE_LOGIN_STATE);
    }
}

export default actions


